<template>
  <div class="setup-index">
    <h1>WPM 初始设置</h1>

    <el-table
      class="setup-item-table"
      :data="items"
      @selection-change="handleSelection"
    >
      <el-table-column type="selection"></el-table-column>

      <el-table-column prop="title" label="设置项">
        <template #default="scope">
          <div :title="scope.row.description">{{ scope.row.title }}</div>
        </template>
      </el-table-column>

      <el-table-column prop="state" label="状态" width="180px">
        <template #default="scope">
          <div :title="scope.row.error">{{ formatState(scope.row.state) }}</div>
        </template>
      </el-table-column>
    </el-table>

    <el-button class="btn-apply" type="success" @click="apply"> 应用</el-button>
  </div>
</template>

<script>
// import MyHeader from "./header.vue";

// const items = [
//   { name: "a", title: "导入预置工程类型", state: "未设置" },
//   { name: "b", title: "导入预置媒体文件", state: "未设置" },
//   { name: "c", title: "导入预置命令模板", state: "未设置" },
//   { name: "d", title: "导入常用程序", state: "未设置" },
// ];

const state_text_map = {
  init: "未设置",
  done: "已经完成",
  want: "未设置",
  have: "已经设置",
  success: "成功",
  error: "有错误",
};

export default {
  name: "setup-index",

  //   components: { MyHeader },

  computed: {
    items() {
      return this.$store.getters["setup/items"];
    },
  },

  data() {
    return {
      selection: [],
    };
  },

  methods: {
    apply() {
      let items = this.getSelectionItems();
      let data = {
        setup_items: items,
      };
      this.$store.dispatch("setup/apply", { data });
    },

    getSelectionItems() {
      let list = this.selection;
      if (list == null) {
        list = [];
      }
      return list;
    },

    handleSelection(selection) {
      // let str = JSON.stringify(selection);
      // console.log("handleSelection:" + str);
      this.selection = selection;
    },

    fetch() {
      this.$store.dispatch("setup/fetch", {});
    },

    formatState(code) {
      let text = state_text_map[code];
      if (text == null) {
        text = code;
      }
      return text;
    },
  },

  mounted() {
    this.fetch();
  },
};
</script>

<style scoped>
.setup-index {
  margin: 20px;
}

.setup-item-table {
  max-width: 640px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 100px;
  margin-bottom: 100px;
}

.btn-apply {
  padding-top: 20px;
  padding-left: 80px;
  padding-right: 80px;
  padding-bottom: 20px;
}
</style>
